<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import { apilogin } from "@/api/index";
import storage from "@/assets/js/storage";
export default {
  name: "App",
  data() {
    return {
      code: ""
    };
  },
  methods: {
    getlogin(){
      
    }
  },
  created() {
    //  apilogin(1).then(res => {
    //           alert(JSON.stringify(res.data));
    //           if (res.data.code == 200) {
    //             storage.set("openID", res.data.ding_user_id);
    //             storage.set("show",res.data.role_health)
    //             storage.set("repair",res.data.role_repair)
    //              this.$router.push({ path: '/home' })
    //             // this.$store.commit("vuex_resetShow", res.data.role_health);
    //             // this.$store.commit("vuex_resetRepair", res.data.role_repair);
    //           }
    //         });
    dd.ready(function() {
      // dd.ready参数为回调函数，在环境准备就绪时触发，jsapi的调用需要保证在该回调函数触发后调用，否则无效。
      try {
        dd.runtime.permission.requestAuthCode({
          corpId: "ding4b76df8fb11dede535c2f4657eb6378f",
          onSuccess: function(result) {
            //通过corpId，code获取userid
           apilogin(result.code).then(res => {
              if (res.data.code == 200) {
                storage.set("openID", res.data.ding_user_id);
                storage.set("show",res.data.role_health)
                storage.set("repair",res.data.role_repair)
                // this.$store.commit("vuex_resetShow", res.data.role_health);
                // this.$store.commit("vuex_resetRepair", res.data.role_repair);
              }
            });
          },
          onFail: function(err) {
            alert(JSON.stringify(err));
          }
        });
      } catch (e) {
        alert(e);
      }
    });
  },
  mounted(){ 
    setTimeout(()=>{
      this.$loading.close();
        this.$router.push({
            path:"/home"
        });
        //延迟时间：3秒
    },2000)   
  }
};
</script>

