import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    //首页
    {
      path: '/',
      name: 'Homeindex',
      component:resolve => require (['@/views/home/index1'],resolve),
    },
    {
      path: '/home',
      name: 'Home',
      component:resolve => require (['@/views/home/index'],resolve),
    },
    //费用查询列表
    {
      path: '/costlist',
      name: 'costList',
      component:resolve => require (['@/views/cost/cost_list'],resolve),
    },
    //水费查询
    {
      path: '/watercost',
      name: 'waterCost',
      component:resolve => require (['@/views/cost/water_cost'],resolve),
    },
    //电费查询
    {
      path: '/electricitycost',
      name: 'electricityCost',
      component:resolve => require (['@/views/cost/electricity_cost'],resolve),
    },
    //其他费用查询
    {
      path: '/othercost',
      name: 'otherCost',
      component:resolve => require (['@/views/cost/other_cost'],resolve),
    },
    //报修申请列表
    {
      path: '/repairlist',
      name: 'repairList',
      component:resolve => require (['@/views/repair/repair_list'],resolve),
    },
    //新增宿舍报修
    {
      path: '/add_repair',
      name: 'addRepair',
      component:resolve => require (['@/views/repair/add_repair'],resolve),
    },
    //新增后勤报修
    {
      path: '/add_logistics_repair',
      name: 'logisticsRepair',
      component:resolve => require (['@/views/repair/add_logistics_repair'],resolve),
    },
    //报修记录
    {
      path: '/repair_history',
      name: 'repairHistory',
      component:resolve => require (['@/views/repair/repair_history'],resolve),
    },
    //卫生检查列表
    {
    path: '/checklist',
    name: 'checkList',
    component:resolve => require (['@/views/check/check_list'],resolve),
  },
    //卫生检查登记
    {
      path: '/sanitationcheck',
      name: 'sanitationCheck',
      component:resolve => require (['@/views/check/sanitation_check'],resolve),
    },
    //卫生检查记录
    {
      path: '/sanitationrecord',
      name: 'sanitationRecord',
      component:resolve => require (['@/views/check/sanitation_record'],resolve),
    },
    //报修处理列表
    {
      path: '/waitrepairlist',
      name: 'waitrepairList',
      component:resolve => require (['@/views/waitrepair/waitrepair_list'],resolve),
    },
    //待处理报修
    {
      path: '/waitrepair',
      name: 'waitRepair',
      component:resolve => require (['@/views/waitrepair/wait_repair'],resolve),
    },
    //待处理报修
    {
      path: '/repairrecord',
      name: 'repairRecord',
      component:resolve => require (['@/views/waitrepair/repair_record'],resolve),
    },
    //报修处理（外修）
    {
      path: '/outsiderepair',
      name: 'outsideRepair',
      component:resolve => require (['@/views/waitrepair/outside_repair'],resolve),
    },
     //报修处理（内修）
     {
      path: '/insiderepair',
      name: 'insideRepair',
      component:resolve => require (['@/views/waitrepair/inside_repair'],resolve),
    },
    {
      path: '/myrecord',
      name: 'myRecord',
      component:resolve => require (['@/views/myrecord/my-record'],resolve),
    },
  ]
})



